@charset "UTF-8";
:root {
  --dark__color-body-bkg: #1c1c1c;
  --dark__color-text: #ccc;
  --dark__color-header: #ddd;
  --dark__color-primary: #ff2929;
  --dark__color-secondary: #ffc845;
  --dark__color-mode-toggle-control-bkg: var(--dark__color-text);
  --dark__font-regular-weight: 300;
  --dark__font-bold-weight: 500;
  --dark__image-filter: brightness(0.8) contrast(1.2);
  --light__color-body-bkg: #fffafa;
  --light__color-text: #545454;
  --light__color-header: #444;
  --light__color-primary: #9a1818;
  --light__color-secondary: #ffc845;
  --light__color-mode-toggle-control-bkg: var(--light__color-text);
  --light__font-regular-weight: 400;
  --light__font-bold-weight: 700;
  --light__image-filter: brightness(1) contrast(1);
  --color__default-green: #3fb950;
  --color__default-purple: #6e40c9;
  --color__default-red: #da3633;
  --color__default_yellow: #ffc845;
  --color__forms-buttons-border: 2px solid var(--dark__color-body-bkg);
  --color__forms-buttons-bkg: #ffc845;
  --color__forms-buttons-text: var(--dark__color-body-bkg);
  --effect__box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  --effect__transition: 0.4s ease-in-out;
  --font__body: "Inter", Arial, Helvetica, sans-serif;
  --unit__body-maxwidth: 1500px;
  --unit__border-radius: 0.75rem;
  --unit__margin-default: 1.5rem 0;
  --unit__padding-left: 3rem;
}

/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role=list],
ol[role=list] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture {
  display: block;
  max-width: 100%;
  height: auto;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
*::before,
*::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
/* Text meant only for screen readers */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
}
.screen-reader-text:focus {
  background-color: #fff;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  color: #000;
  display: block;
  font-size: 0.875rem;
  font-weight: 700;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

html {
  font-size: 62.5%;
  line-height: 1.5;
  -webkit-text-size-adjust: 100%;
}

body {
  --color__body-text: var(--light__color-text);
  --color__body-background: var(--light__color-body-bkg);
  --color__header-text: var(--light__color-header);
  --color__link-body: var(--color__body-text);
  --color__link-body-hover: var(--light__color-primary);
  --color__link-body-underline: var(--color__body-text);
  --color__link-body-underline-hover: var(--light__color-secondary);
  --color__mode-toggle-control-bkg: var(--light__color-mode-toggle-control-bkg);
  --color__primary: var(--light__color-primary);
  --color__secondary: var(--light__color-secondary);
  --effect__image-filter: var(--light__image-filter);
  --font__regular-weight: var(--light__font-regular-weight);
  --font__bold-weight: var(--light__font-bold-weight);
}

body.dark-theme {
  --color__body-text: var(--dark__color-text);
  --color__body-background: var(--dark__color-body-bkg);
  --color__header-text: var(--dark__color-header);
  --color__link-body: var(--color__body-text);
  --color__link-body-hover: var(--dark__color-secondary);
  --color__link-body-underline: var(--color__body-text);
  --color__link-body-underline-hover: var(--dark__color-primary);
  --color__mode-toggle-control-bkg: var(--dark__color-mode-toggle-control-bkg);
  --color__primary: var(--dark__color-primary);
  --color__secondary: var(--dark__color-secondary);
  --effect__image-filter: var(--dark__image-filter);
  --font__regular-weight: var(--dark__font-regular-weight);
  --font__bold-weight: var(--dark__font-bold-weight);
}

@media (prefers-color-scheme: dark) {
  body {
    --color__body-text: var(--dark__color-text);
    --color__body-background: var(--dark__color-body-bkg);
    --color__header-text: var(--dark__color-header);
    --color__link-body: var(--color__body-text);
    --color__link-body-hover: var(--dark__color-secondary);
    --color__link-body-underline: var(--color__body-text);
    --color__link-body-underline-hover: var(--dark__color-primary);
    --color__mode-toggle-control-bkg: var(--dark__color-mode-toggle-control-bkg);
    --color__primary: var(--dark__color-primary);
    --color__secondary: var(--dark__color-secondary);
    --effect__image-filter: var(--dark__image-filter);
    --font__regular-weight: var(--dark__font-regular-weight);
    --font__bold-weight: var(--dark__font-bold-weight);
  }

  body.light-theme {
    --color__body-text: var(--light__color-text);
    --color__body-background: var(--light__color-body-bkg);
    --color__header-text: var(--light__color-header);
    --color__link-body: var(--color__body-text);
    --color__link-body-hover: var(--light__color-primary);
    --color__link-body-underline: var(--color__body-text);
    --color__link-body-underline-hover: var(--light__color-secondary);
    --color__mode-toggle-control-bkg: var(--light__color-mode-toggle-control-bkg);
    --color__primary: var(--light__color-primary);
    --color__secondary: var(--light__color-secondary);
    --effect__image-filter: var(--light__image-filter);
    --font__regular-weight: var(--light__font-regular-weight);
    --font__bold-weight: var(--light__font-bold-weight);
  }
}
::selection {
  background: #ffc845;
  color: #000;
}

* {
  font-family: var(--font__body);
}

body {
  background: var(--color__body-background);
  font-size: clamp(1.8rem, 4vw, 2.5rem);
  font-weight: var(--font__regular-weight);
  color: var(--color__body-text);
  overflow-x: hidden;
  animation: fadeInAnimation ease 2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
img {
  filter: var(--effect__image-filter);
}

p {
  margin: var(--unit__margin-default);
}

strong {
  font-weight: var(--font__bold-weight);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: var(--font__bold-weight);
  color: var(--color__header-text);
}

h1 {
  font-size: clamp(4rem, 4.5vw, 6rem);
  line-height: 1.25;
  margin: 1.25em 0 0.75em 0;
}

h2 {
  font-size: clamp(3.33rem, 4vw, 5rem);
  line-height: 1.25;
  margin: var(--unit__margin-default);
}

a {
  color: var(--color__link-body);
  text-decoration-color: var(--color__link-body-underline);
  transition: var(--effect__transition);
}
a:hover, a:focus {
  color: var(--color__link-body-hover);
  text-decoration-color: var(--color__link-body-underline-hover);
}

a[href^="mailto:"]:after,
a[target=_blank]:after {
  display: inline-block;
  position: relative;
  top: 1px;
  left: 2px;
  content: "↑";
  font-size: 90%;
  color: inherit;
  transform: rotate(45deg);
  width: 1em;
  height: 1em;
}

ol,
ul {
  margin-block-start: 0;
  margin-block-end: 0;
  padding-inline-start: 0;
}

ol,
ul {
  display: block;
  margin: var(--unit__margin-default);
}

ol {
  padding-left: var(--unit__padding-left);
}

ol.numbered {
  list-style-type: decimal;
}

ul.arrow li:before {
  display: inline-block;
  position: relative;
  top: -2px;
  left: 0;
  content: "→";
  font-size: 90%;
  color: inherit;
  width: 1em;
  height: 1em;
  margin-right: 10px;
}

#mode-toggle-container .mode-toggle {
  display: inline-grid;
  grid-template-areas: "input label";
  grid-gap: 0;
  align-items: center;
  justify-self: center;
  cursor: pointer;
}
#mode-toggle-container .mode-toggle-control {
  border-radius: 1em;
  background-color: var(--color__mode-toggle-control-bkg);
  display: inline-grid;
  align-items: center;
  opacity: 0.8;
}
#mode-toggle-container .mode-toggle-control,
#mode-toggle-container .mode-toggle input {
  font-size: 2rem;
  width: 4rem;
  height: 1.6rem;
  grid-area: input;
}
#mode-toggle-container .mode-toggle input {
  opacity: 0;
}
#mode-toggle-container input:checked + .mode-toggle-control:before {
  --color__switch-toggle-button-bkg: var(--color__default-green);
  --unit__switch-x: 100%;
}
#mode-toggle-container .mode-toggle-control:before {
  --color__switch-toggle-button-bkg: var(--color__default-red);
  --unit__switch-x: 0%;
  content: "";
  width: 1em;
  height: 1em;
  border-radius: 50%;
  background-color: var(--color__switch-toggle-button-bkg);
  transform: translateY(-8%) translateX(var(--unit__switch-x));
  transition: var(--effect__transition);
  border: 4px solid #000;
}

button {
  border-width: 0;
  border-style: none;
  border-color: none;
}

button {
  background-color: var(--color__forms-buttons-bkg);
  font-weight: 400;
  color: var(--color__forms-buttons-text);
  border: var(--color__forms-buttons-border);
  border-radius: var(--unit__border-radius);
  padding: 1rem;
  cursor: pointer;
  transition: var(--effect__transition);
}

button.squishy:active {
  transform: scale(0.95);
}

header {
  line-height: 1.2;
}
header .container {
  display: flex;
  align-items: center;
  width: 90%;
  max-width: var(--unit__body-maxwidth);
  padding: 1.5rem 0;
  margin: 0 auto;
}
header .container .logo {
  display: flex;
  align-items: center;
}
header .container .logo img {
  border-radius: 50%;
}
header .container .logo div:nth-child(2) {
  margin: 0 1rem;
}
header .container .logo div:nth-child(2) span {
  display: block;
}
@media (max-width: 480px) {
  header .container .logo div:nth-child(2) span {
    display: none;
  }
}
header .container #mode-toggle-container {
  margin-left: auto;
}

main {
  display: grid;
  grid-template-columns: 1fr min(50ch, 100%) 1fr;
  max-width: 90%;
  margin: 0 auto 3em auto;
}
main > * {
  grid-column: 2;
}

footer .container {
  display: flex;
  align-items: center;
  width: 90%;
  max-width: var(--unit__body-maxwidth);
  margin: 0 auto;
  padding: 1rem 0;
  border-top: 2px solid var(--color__default_yellow);
}
footer .container .info {
  margin: 15px 0;
}
footer .container .nav {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;
}
footer .container .nav nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
footer .container .nav nav ul li {
  display: inline;
}
footer .container .nav nav ul li:not(:first-child) {
  padding-left: 36px;
}
@media (max-width: 1023px) {
  footer .container .nav nav ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  footer .container .nav nav ul li:not(:first-child) {
    padding-left: 0;
  }
}
@media (max-width: 480px) {
  footer .container .nav nav ul {
    margin-top: 15px;
  }
  footer .container .nav nav ul li {
    padding-left: 0;
  }
}
@media (max-width: 480px) {
  footer .container {
    flex-direction: column;
  }
  footer .container .info {
    order: 2;
  }
  footer .container .nav {
    order: 1;
  }
}

html,
body {
  scrollbar-width: auto;
  scrollbar-color: var(--color__default_yellow) var(--light__color-text) !important;
}

::-webkit-scrollbar {
  width: 16px;
  height: 16px;
}

::-webkit-scrollbar-track {
  background: var(--light__color-text);
}

::-webkit-scrollbar-thumb {
  background-color: var(--color__default_yellow);
  border-radius: 0;
  border: 2px solid var(--light__color-text);
}

.barrd-picture {
  border-radius: 50%;
  margin: 0 auto;
  box-shadow: var(--effect__box-shadow);
}