html,
body {
  scrollbar-width: auto;
  scrollbar-color: var(--color__default_yellow) var(--light__color-text) !important;
}

::-webkit-scrollbar {
  width: 16px;
  height: 16px;
}
::-webkit-scrollbar-track {
  background: var(--light__color-text);
}
::-webkit-scrollbar-thumb {
  background-color: var(--color__default_yellow);
  border-radius: 0;
  border: 2px solid var(--light__color-text);
}
